import React, { Component } from 'react';
import './App.css';
import Todo from './Todo';
import NewTodo from './NewTodo';

var apiKey = "cc2dd1-349dc4-e4d50c-d4c8b4-5ea8df";
var api = "https://cse204.work/todos";

class App extends Component {

  constructor(props){
    super(props);
    this.state = {
      todos: []
    }
  }

  render() {
    return (
      <div className="App">
        <div id="todos">
            <h1>ToDo List</h1>
            <NewTodo input_id="newTitle" button_id="addBtn" handleAddEvent={this.handleAddEvent} /> 
            <div id="todo-sort-by">
              <a>Sort by:<br></br></a>
              <select id="todo-sort-select" onChange={this.handleSort.bind(this)}>
                <option id="default">Default</option>
                <option id="created">Created</option>
                <option id="completed">Not Completed</option>
              </select>
          </div>           
        </div>
        <div id="todos">
          <Todo todos={this.state.todos} handleComplete={this.handleComplete} handleDelete={this.handleDelete} />
        </div>
    
      </div>
    );
  }

  componentWillMount() {
    this.renderToDo();
  }

  renderToDo() {
    var xhttp = new XMLHttpRequest()
    var todoList = [];

    xhttp.onreadystatechange = function () {
      if (this.readyState === 4 && this.status === 200) {
        // Convert response to JSON
        todoList = JSON.parse(this.responseText);
      }
    }
    xhttp.open("GET", api, false);
    xhttp.setRequestHeader("x-api-key", apiKey);
    xhttp.send();
    console.log(todoList);
    this.setState({ todos: todoList });
  }

  handleComplete = (id) => {
    this.setState({
      todos: this.state.todos.map((todo) => {
        if (todo.id === id) {
          todo.completed = !todo.completed;
          var xhttp = new XMLHttpRequest();

          var data = {
            completed: todo.completed
          };

          // Send the request to API
          xhttp.onreadystatechange = function () {
            if (this.readyState === 4 && this.status === 200) {
            }
          }
          xhttp.open("PUT", api + "/" + id, true);
          xhttp.setRequestHeader("x-api-key", apiKey);
          xhttp.send(JSON.stringify(data));
        }
        return todo;
      })
    });
  }

  handleSort = (e) => {
    var todoList = document.getElementsByClassName("incomplete");
    console.log(todoList);
    
  }

  handleAddEvent = (e) => {
    if (document.getElementById("newTitle").value === null || document.getElementById("newTitle").value === "") {
      alert("Please input an event");
    } else {
      var xhttp = new XMLHttpRequest();
      var newTodo = document.getElementById("newTitle").value;

      var data = {
        text: newTodo
      };

      xhttp.open("POST", api, false);
      xhttp.setRequestHeader("Content-type", "application/json");
      xhttp.setRequestHeader("x-api-key", apiKey);
      xhttp.send(JSON.stringify(data));

      xhttp.onreadystatechange = function () {
        if (this.readyState === 4 && this.status === 200) {
          
        } else if (this.readyState === 4) {
          
        }
      };

      document.getElementById("newTitle").value = "";

      this.renderToDo();
    }
  }

  handleDelete = (id) => {
    var xhttp = new XMLHttpRequest();

    // Request to API to delete the item
    xhttp.onreadystatechange = function () {
      if (this.readyState === 4 && this.status === 200) {
        // console.log("success");
      }
    }

    xhttp.open("DELETE", api + "/" + id, false);
    xhttp.setRequestHeader("x-api-key", apiKey);
    xhttp.send();

    this.renderToDo();
  }
}

export default App;
